    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

body {
    background-color: #faf0ca;
}
#content {
    border-collapse: collapse;
    border-radius: 10px;
    height: 90vh;
    width: 40vw;
    margin: auto;
    padding-top: 2rem;
    padding-bottom: 2rem;

    display: grid;
    /* grid-template-columns: repeat(2, 1fr); */
    grid-template-rows: .5fr 5fr;


    header {
        grid-row: 1 / 2;
        grid-column: 1 / -1;
        margin: auto;
        color: #fff;
        margin-bottom: 2rem;

        #cityInput {
            height: 2rem;
            width: 50%;
            margin-right: 1rem;
            padding-left: 1rem;
            font-size: 1.25rem;
            border-radius: 10px;
            outline: none;
            border: 2px solid #2f3c7e
        }

        #cityInputBtn {
            border-radius: 1rem;
            border: none;
            background-color: #2f3c7e;
            font-size: 1rem;
            padding: .5rem 1rem;
            color: white;
        }
        #cityInputBtn:focus {
            outline: 5px solid #5e72d6;

        }
    }

    main {
        background-color: white;
        border-radius: 1rem;
        grid-row: 2 / -1;
        grid-column: 1 / -1;
        
        display: grid;
        grid-template-rows: .75fr .75fr .25fr;
        grid-template-columns: repeat(2, 1fr);

        #heroSec {
            grid-column: 1 / 2;
            grid-row: 1 / 2;

            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: 1fr 2fr 1fr;

            #cityName {
                grid-column: 1 / -1;
                grid-row: 1 / 2;
                text-align: center;
                font-size: 1.25rem;
                align-self: center;
            }

            #weatherPngSec {
                grid-row: 2 / -1;
                display: flex;
                justify-content: center;
                align-items: center;
                align-self: center;
                img {
                    height: 100%;
                }
            }

            #weatherValue {
                grid-column: 2 / -1;
                grid-row: 2 / -2;
                align-self: center;
                font-size: 7rem;
                padding-right: 2rem;
            }
            #weatherValue::after {
                content: "°";
                font: 1rem;
            }
            #weatherText {
                font-size: 1rem;
            }
        }

        #humiditySec {
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        }

        #precipitationSec {
            grid-column: 1 / -1;
            grid-row: 3 / 4;
        }

        #feelslikeSec {
            grid-column: 2 / -1;
            grid-row: 1 / 2;
        }

        #windSec {
            grid-column: 2 / -1;
            grid-row: 2 / 3;
        }

    }
    main > section {
        border: 2px solid #2f3c7e;
        border-radius: 1rem;
        display: grid;
        grid-template-rows: 3fr 1fr;

        .value {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 4rem;
        }
        .secLabel {
            grid-row: 2 / -1;
            text-align: center;
        }
    }
}


#loaderDiv {
    display: flex;
    justify-content: center;
    align-items: center;   
}


#loader {
    content: "";
    border: 1rem solid white;
    border-top: 1rem solid #2f3c7e;
    border-radius: 50%;
    /* transform: border-top-color 300ms ease-in-out; */
    animation: rotateLoader 2s linear infinite;
    height: 5rem;
    width: 5rem;


}


@keyframes rotateLoader {

    0% {
        transform: rotate(0turn);
    }
    100% {
        transform: rotate(1turn);
    }
    
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,uBAAuB;AAC3B;;AAEA;IACI,yBAAyB;AAC7B;AACA;IACI,yBAAyB;IACzB,mBAAmB;IACnB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,oBAAoB;;IAEpB,aAAa;IACb,2CAA2C;IAC3C,4BAA4B;;;IAG5B;QACI,eAAe;QACf,mBAAmB;QACnB,YAAY;QACZ,WAAW;QACX,mBAAmB;;QAEnB;YACI,YAAY;YACZ,UAAU;YACV,kBAAkB;YAClB,kBAAkB;YAClB,kBAAkB;YAClB,mBAAmB;YACnB,aAAa;YACb;QACJ;;QAEA;YACI,mBAAmB;YACnB,YAAY;YACZ,yBAAyB;YACzB,eAAe;YACf,mBAAmB;YACnB,YAAY;QAChB;QACA;YACI,0BAA0B;;QAE9B;IACJ;;IAEA;QACI,uBAAuB;QACvB,mBAAmB;QACnB,gBAAgB;QAChB,mBAAmB;;QAEnB,aAAa;QACb,qCAAqC;QACrC,qCAAqC;;QAErC;YACI,kBAAkB;YAClB,eAAe;;YAEf,aAAa;YACb,qCAAqC;YACrC,+BAA+B;;YAE/B;gBACI,mBAAmB;gBACnB,eAAe;gBACf,kBAAkB;gBAClB,kBAAkB;gBAClB,kBAAkB;YACtB;;YAEA;gBACI,gBAAgB;gBAChB,aAAa;gBACb,uBAAuB;gBACvB,mBAAmB;gBACnB,kBAAkB;gBAClB;oBACI,YAAY;gBAChB;YACJ;;YAEA;gBACI,mBAAmB;gBACnB,gBAAgB;gBAChB,kBAAkB;gBAClB,eAAe;gBACf,mBAAmB;YACvB;YACA;gBACI,YAAY;gBACZ,UAAU;YACd;YACA;gBACI,eAAe;YACnB;QACJ;;QAEA;YACI,kBAAkB;YAClB,eAAe;QACnB;;QAEA;YACI,mBAAmB;YACnB,eAAe;QACnB;;QAEA;YACI,mBAAmB;YACnB,eAAe;QACnB;;QAEA;YACI,mBAAmB;YACnB,eAAe;QACnB;;IAEJ;IACA;QACI,yBAAyB;QACzB,mBAAmB;QACnB,aAAa;QACb,2BAA2B;;QAE3B;YACI,aAAa;YACb,uBAAuB;YACvB,mBAAmB;YACnB,eAAe;QACnB;QACA;YACI,gBAAgB;YAChB,kBAAkB;QACtB;IACJ;AACJ;;;AAGA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;;AAGA;IACI,WAAW;IACX,wBAAwB;IACxB,8BAA8B;IAC9B,kBAAkB;IAClB,mDAAmD;IACnD,0CAA0C;IAC1C,YAAY;IACZ,WAAW;;;AAGf;;;AAGA;;IAEI;QACI,wBAAwB;IAC5B;IACA;QACI,wBAAwB;IAC5B;;AAEJ","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    font-family: sans-serif;\n}\n\nbody {\n    background-color: #faf0ca;\n}\n#content {\n    border-collapse: collapse;\n    border-radius: 10px;\n    height: 90vh;\n    width: 40vw;\n    margin: auto;\n    padding-top: 2rem;\n    padding-bottom: 2rem;\n\n    display: grid;\n    /* grid-template-columns: repeat(2, 1fr); */\n    grid-template-rows: .5fr 5fr;\n\n\n    header {\n        gr